@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

a {
  color: black;
  position: relative; }

a::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -1;
  width: 100%;
  background-color: black;
  height: 15%;
  transition: height .2s, background-color .1s; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'Avengeance';
  src: url("/portfolio-react-app/static/media/AVENGEANCE HEROIC AVENGER.1c034a3c.ttf") format("truetype");
  font-weight: normal;
  font-size: normal; }

body {
  width: 80vw;
  max-width: 100vw;
  margin: auto;
  margin-top: 7vh;
  font-family: 'Avengeance';
  box-sizing: border-box; }

h1 {
  font-size: 1.8em;
  font-weight: 550; }

h2 {
  font-size: 0.8em; }

.Background {
  background-image: url(/portfolio-react-app/static/media/About.eeffd5d2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  height: 100vh;
  padding: 5vw;
  color: white; }

p {
  font-family: 'Space Mono', monospace;
  line-height: 1.1em;
  font-size: 1.1em;
  margin: 1em;
  color: white;
  font-weight: 600; }

.Heading {
  text-align: right;
  color: black;
  font-size: 2.6em; }

.Social i {
  margin: .2em; }

.Social {
  font-size: 2.5em;
  display: flex;
  flex-direction: column;
  position: fixed;
  left: 5vh;
  top: 50vh;
  color: black; }

a {
  color: black;
  position: relative;
  text-decoration: none; }

a:hover::after {
  height: 100%;
  background-color: #E25358; }

.Space {
  content: "";
  height: 25vh; }

.Nav {
  background-color: white;
  display: block;
  position: fixed;
  top: 2vh;
  left: 10vw;
  font-size: 1.5em;
  text-transform: lowercase;
  z-index: 99; }
  .Nav ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 0.3em;
    margin: 0; }
  .Nav .Link {
    color: black;
    position: relative; }
  .Nav .Link::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    width: 100%;
    background-color: black;
    height: 15%;
    transition: height .2s, background-color .1s; }
  .Nav .Link:hover::after {
    height: 110%;
    background-color: #E25358; }
  .Nav li {
    padding-left: 1%;
    padding-right: 1%;
    margin-right: 1rem;
    margin-left: 1rem; }

.Home {
  background-image: url(/portfolio-react-app/static/media/Home.b9718a82.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  height: 100vh;
  font-size: 3em;
  padding: 5vw;
  color: white; }

.Container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

/* Google Fonts */
/* Global */
.line-1 {
  margin: 0 auto;
  border-right: 2px solid rgba(255, 255, 255, 0.75);
  margin-top: .4em;
  font-size: .5em;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-family: 'Anonymous Pro', monospace; }

/* Animation */
.anim-typewriter {
  -webkit-animation: typewriter 2s steps(20) 0.5s 1 normal both, blinkTextCursor 500ms steps(20) infinite normal;
          animation: typewriter 2s steps(20) 0.5s 1 normal both, blinkTextCursor 500ms steps(20) infinite normal; }

@-webkit-keyframes typewriter {
  from {
    width: 0; }
  to {
    width: 17em; } }

@keyframes typewriter {
  from {
    width: 0; }
  to {
    width: 17em; } }

@-webkit-keyframes blinkTextCursor {
  from {
    border-right-color: rgba(255, 255, 255, 0.75); }
  to {
    border-right-color: transparent; } }

@keyframes blinkTextCursor {
  from {
    border-right-color: rgba(255, 255, 255, 0.75); }
  to {
    border-right-color: transparent; } }

.Container i {
  font-size: .3em; }

.Group {
  visibility: hidden; }

.Images {
  width: 100%;
  height: auto; }

.Gif {
  opacity: 0.25;
  width: 15%;
  position: absolute;
  right: 50px;
  top: -5px; }

.Skills {
  text-align: center; }

.Skills h2 {
  margin-top: 1em;
  font-size: 1em;
  font-family: 'Space Mono', monospace;
  color: yellow; }

.Slider img {
  width: 100%; }

.Slider i {
  font-size: 4em;
  margin: .5em; }

.Flex {
  display: flex;
  flex-direction: row; }

#Roadmap {
  color: orange;
  text-decoration: underline;
  font-size: 1.5em; }

@media (min-width: 0) and (max-width: 425px) {
  .Social {
    display: none; }
  .line-1 {
    font-size: .3em; }
  @-webkit-keyframes typewriter {
    from {
      width: 0; }
    to {
      width: 17em; } }
  @keyframes typewriter {
    from {
      width: 0; }
    to {
      width: 17em; } }
  .Nav {
    font-size: 1.2em; }
  body {
    width: 100%; } }

